  width: 100%;
  background: #FFFFFF;
  opacity: 0.5;
  border: 1px solid #A0BCC8;
  border-radius: 5px;
  font-weight: 400;
  font-size: 16px;
  line-height: 139.9%;
  letter-spacing: 0.04em;
  color: #213540;
  padding: 11px 25px;
}
.footer__form .wpcf7 span textarea {
  height: 140px;
}
@media all and (max-width: 1600px) {
  .footer__form .wpcf7 span textarea {
    height: 120px;
  }
}
@media all and (max-width: 500px) {
  .footer__form .wpcf7 span textarea {
    height: 100px;
  }
}
.footer__img {
  width: calc(50% - 60px);
}
@media all and (max-width: 1024px) {
  .footer__img {
    display: none;
  }
}
.footer__map {
  width: calc(50% - 60px);
  height: 100%;
  transition: 1s ease-in-out;
}
@media all and (min-width: 1025px) {
  .footer__map {
    -webkit-mask-image: url(../../img/footer/map-mask.png);
    mask-image: url(../../img/footer/map-mask.png);
    -webkit-mask-position: center center;
    -webkit-mask-size: contain;
    -webkit-mask-repeat: no-repeat;
    mask-repeat: no-repeat;
    mask-position: center center;
  }
}
@media all and (max-width: 1024px) {
  .footer__map {
    width: 100%;
    order: 1;
    height: 50vh;
  }
}
.footer__title {
  margin-bottom: 23px;
}
.footer__bottom {
  display: flex;
  position: absolute;
  width: 100%;
  left: 0;
  align-items: center;
  top: calc(100vh - 38px);
}
@media all and (max-width: 1600px) {
  .footer__bottom {
    top: auto;
    bottom: 15px;
  }
}
@media all and (max-width: 1024px) {
  .footer__bottom {
    flex-direction: column;
    position: static;
    margin-bottom: 15px;
    align-items: flex-start;
    padding: 0;
    order: 3;
  }
}
@media all and (max-width: 500px) {
  .footer__bottom {
    align-items: center;
  }
}
.footer__copyright {
  flex: 1;
  font-weight: 400;
  font-size: 16px;
  line-height: 139.9%;
  color: #A0BCC8;
}
@media all and (max-width: 1024px) {
  .footer__copyright {
    order: 3;
  }
}
@media all and (max-width: 768px) {
  .footer__copyright {
    font-size: 15px;
  }
}
@media all and (max-width: 500px) {
  .footer__copyright {
    font-size: 12px;
  }
}
.footer__phone, .footer__mail {
  text-decoration: none;
  font-weight: 400;
  font-size: 16px;
  line-height: 139.9%;
  letter-spacing: 0.04em;
  color: #213540;
  margin-left: 49px;
}
@media all and (max-width: 1024px) {
  .footer__phone, .footer__mail {
    margin-left: 0;
  }
}
@media all and (max-width: 500px) {
  .footer__phone, .footer__mail {
    font-size: 16px;
  }
}
@media all and (max-width: 1024px) {
  .footer__phone {
    order: 1;
  }
}
@media all and (max-width: 1024px) {
  .footer__mail {
    order: 2;
    margin-bottom: 20px;
  }
}
.footer__contacts {
  display: flex;
  flex-direction: column;
  margin-bottom: 28px;
}
@media all and (max-height: 900px) {
  .footer__contacts {
    margin-bottom: 8px;
  }
}
.footer__contacts-row {
  display: flex;
  align-items: center;
  text-decoration: none;
  font-family: "Nunito Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 139.9%;
  color: #000000;
  margin-bottom: 12px;
}
.footer__contacts-row svg {
  margin-right: 15px;
}
.footer__success {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 99999;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer__success__container {
  position: relative;
  max-width: 300px;
  width: calc(100% - 40px);
  height: auto;
  background-color: #FFFFFF;
  border: 2px solid #BFD9EB;
  box-shadow: 0px 69px 85px -77px rgba(193, 219, 237, 0.25);
  border-radius: 30px;
  padding: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.footer__success__container p {
  font-size: 24px;
  margin-bottom: 0;
}
.footer__modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 99999;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer__modal-container {
  position: relative;
  max-width: 560px;
  width: calc(100% - 40px);
  height: auto;
  background-color: #FFFFFF;
  border: 2px solid #BFD9EB;
  box-shadow: 0px 69px 85px -77px rgba(193, 219, 237, 0.25);
  border-radius: 30px;
  padding: 60px 70px 0 70px;
}
@media all and (max-width: 768px) {
  .footer__modal-container {
    padding: 40px 40px 0 40px;
  }
}
@media all and (max-width: 500px) {
  .footer__modal-container {
    padding: 40px 20px 0 20px;
  }
}
.footer__modal-container form br {
  display: none;
}
.footer__modal-container form p {
  margin-bottom: 15px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.footer__modal-container form p .wpcf7-spinner {
  margin-top: 20px;
}
.footer__modal-container form .wpcf7-response-output {
  font-size: 14px;
}
.footer__modal-container form .wpcf7-submit {
  margin-top: 25px;
}
@media all and (max-width: 500px) {
  .footer__modal-container form .wpcf7-submit {
    width: 100%;
  }
}
.footer__modal-container form label {
  font-family: "Nunito Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 139.9%;
  color: #A0BCC8;
  width: 100%;
  text-align: left;
  display: block;
}
.footer__modal-container form label input, .footer__modal-container form label textarea {
  background: #FFFFFF;
  border: 1px solid #A0BCC8;
  border-radius: 5px;
  font-family: "Nunito Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 139.9%;
  letter-spacing: 0.04em;
  color: #213540;
  width: 100%;
  padding: 11px 25px;
}
.footer__modal-container form label textarea {
  height: 74px;
}
.footer__modal-close {
  position: absolute;
  right: 20px;
  top: 20px;
  cursor: pointer;
}
.footer__modal-close svg circle, .footer__modal-close svg rect {
  transition: 0.2s;
}
.footer__modal-close:hover svg circle {
  opacity: 1;
  fill: #ec1c24;
}
.footer__modal-close:hover svg rect {
  fill: white;
}
.footer__modal__socials {
  margin-left: -18px;
}
.footer__modal__socials a {
  margin-right: -14px;
  text-decoration: none;
}
.footer__modal-title {
  text-align: center;
  font-size: 36px;
  margin-bottom: 19px;
  text-align: left;
}

.to-up {
  position: fixed;
  bottom: 62px;
  right: 62px;
  z-index: 50;
  width: 106px;
  height: auto;
}
@media all and (max-width: 1400px) {
  .to-up {
    right: 75px;
    bottom: 75px;
  }
  .to-up svg {
    width: 100%;
    height: 100%;
  }
}
@media all and (max-width: 1024px) {
  .to-up {
    width: 100px;
    height: 100px;
  }
}
@media all and (max-width: 768px) {
  .to-up {
    right: 15px;
    bottom: 15px;
    width: 90px;
    height: 90px;
  }
}
@media all and (max-width: 500px) {
  .to-up {
    right: 0px;
    bottom: 0px;
    width: 70px;
    height: 70px;
  }
}